import React from 'react';

export default ({ by, children }) => (
  <blockquote>
    {children}
    <p className="byline">{by}</p>
    <style jsx>{`
      .byline {
        margin-top: .5rem;
        text-align: right;
        font-weight: 300;
        font-style: italics;
      }
    `}
    </style>
  </blockquote>
);



